.App {
  /* background-image: url("../public/Frame.png"); */
  /* background-size: cover; */
  background-size: 945px 697px;
  /* background-size: auto, auto; */
  background-repeat: no-repeat;
  background-position: top;
}
#reels {
  display: flex;
  width: 100vw;
  height: calc((3 / 5) * 100vw);
  max-height: calc(90vh - 50px - 40px);
  max-width: calc((5 / 3) * (90vh - 50px - 40px));
}

.reel {
  overflow: hidden;
  width: 20%;
  height: 100%;
  position: relative;
}

.reel > .icons {
  position: relative;
}

.reel > .icons > img {
  width: calc(100% + 6px);
  margin: -3px 0 0 -3px;
  height: calc(100% - 200px);

  /* enable gpu accelaration, fixes iOS flicker */
  transform: translate3d(0, 0, 0);
}

#controls {
  /* background-color: rgba(255, 255, 255, 0.9); */
  /* background-color: #bf2fba; */
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 30px;
}

#controls label {
  display: flex;
  align-items: center;
  margin: 0;
}

#controls label input {
  margin-right: 5px;
}

input[type="checkbox"] {
  width: 40px;
  height: 40px;
}

#jackpot {
  color: #d5ad6d;
  font-size: 40px;
  text-align: center;
}

#slot.inverted .reel {
  transform: scaleY(-1);
}

#slot.inverted .reel > .icons > img {
  transform: scaleY(-1);
}

#slot {
  text-align: center;
}

.icons {
  display: flex;
  justify-content: center;
  flex-direction: column;
  /* Add other styles as necessary */
}
.inverted {
  transform: rotate(180deg);
}
