/* src/App.css */
.deck {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 10px;
  margin: 5px;
  width: 60px;
  height: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  background-color: white;
}

.rank {
  font-size: 24px;
  font-weight: bold;
}

.suit {
  font-size: 24px;
  color: red;
}
